    .mobile_btn {
        display: block;
        cursor: pointer;
        z-index: 10;
    }

    .container{
        height: 100%;
    }
    .menu {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        height: 100vh;
        background: #9CA3AF;
        padding: 0;
        transform: translateX(100%);
        transition: transform 0.5s;
        z-index: 10;
        padding: 0 90px;
        background-image: url(../images/menu-pic.png);
        background-repeat: no-repeat;
        background-position: bottom right;
        min-height: 100vh;
   
    }

    .active {
        transform: translateX(0%);

    }

    @media screen and (max-height: 900px) {
        .menu{
            background-size: 35%;
        }
    }

